<template>

    <div id="app" >
      <h1 v-text="title"></h1>
      <div v-show="showNext" class="next">
        <a href="https://microzz.com/about.html" target="_blank">>>跳过</a>
      </div>

      <div class="name">
        <span v-text="name"></span>
      </div>

      <div class="major">
        <span v-text="major"></span>
      </div>

      <div class="intro">
        <span v-text="intro"></span>
      </div>

      <div class="skills">
        <div v-text="skill" class="skill"></div>
        <div v-text="fe" class="fe"></div>
        <div v-text="be" class="be"></div>
        <div v-text="db" class="db"></div>
        <div v-text="tool" class="tool"></div>
        <div v-text="git" class="git"></div>
      </div>

      <div class="achievements">
        <div v-text="achievement" class="achievement"></div>
        <div  class="vue">
          <a v-text="vue" href="https://microzz.com/2017/04/08/vue-music2/" target="_blank"></a>
        </div>
        <div  class="react">
          <a v-text="react" href="https://microzz.com/2017/03/29/react-news/" target="_blank"></a>
        </div>
        <div  class="npm">
          <a v-text="npm" href="https://microzz.com/2017/03/12/select-plugin/" target="_blank"></a>
        </div>
        <div  class="reader">
          <a v-text="reader" href="https://microzz.com/2017/03/04/WebReader/" target="_blank"></a>
          <a v-text="more" href="https://microzz.com/about.html" target="_blank"></a>
        </div>


      </div>

      <transition name="github">
        <div class="github" v-show="showGitHub">
          <span>
            GitHub：<a href="https://github.com/microzz" target="_blank">https://github.com/microzz</a>
          </span>
        </div>
      </transition>

      <transition name="website">
        <div class="website" v-show="showWebsite">
          <span>
            个人网站：<a href="https://microzz.com/" target="_blank">https://microzz.com</a>
          </span>
        </div>
      </transition>

      <transition name="email">
        <div class="email" v-show="showEmail">
          <span>
            E-mail：<a href="mailto:zhaohui@microzz.com" target="_blank">zhaohui@microzz.com</a>
          </span>
        </div>
      </transition>

      <div class="go" v-bind:class="{'go-plus': goPlus}">
        <a href="https://microzz.com/about.html" target="_blank"><i></i></a>
      </div>
    </div>

</template>

<script>

export default {
  name: 'app',
  mounted() {

    this.change('title')
      .then(() => this.change('name'))
      .then(() => this.change('major'))
      .then(() => this.change('intro'))
      .then(() => this.change('skill'))
      .then(() => this.change('fe'))
      .then(() => this.change('be'))
      .then(() => this.change('db'))
      .then(() => this.change('tool'))
      .then(() => this.change('git'))
      .then(() => this.change('achievement'))
      .then(() => this.change('vue'))
      .then(() => this.change('react'))
      .then(() => this.change('npm'))
      .then(() => this.change('reader'))
      .then(() => this.change('more'))
      .then(() => this.show('GitHub'))
      .then(() => this.show('Website'))
      .then(() => this.show('Email'))
      .then(() => this.goPlus = true);

      // setInterval(() => {
      //   this.showNext = !this.showNext;
      // }, 5000);


  },
  data() {
    return {
      title: '',
      name: '',
      major: '',
      intro: '',
      skill: '',
      fe: '',
      be: '',
      db: '',
      tool: '',
      git: '',
      intro: '',
      achievement: '',
      vue: '',
      react: '',
      npm: '',
      reader: '',
      more: '',
      infos: {
        title: ` 关于我 `,
        name: `姓 名：赵辉 `,
        major: `专 业：软件工程 `,
        skill: `技 能： `,
        fe: `【前端】Vue.js、React.js、JavaScript(ES6、ES5)、HTML/HTML5、CSS/CSS3、SASS(SCSS)、微信小程序、Socket.IO、jQuery、Ajax/fetch `,
        be: `【后端】Node.js(Express.js、Koa.js) `,
        db: `【数据库】MongoDB、MySQL `,
        tool: `【自动化构建】Webpack、Gulp `,
        git: `【版本管理】Git `,
        intro: `简介：目前是一个待业的大三码农，求工作（＾_＾）`,
        achievement: `作品(部分)：`,
        vue: `【Vue.js】Vue音乐播放器`,
        react: `【React.js】React新闻App`,
        npm: `【npm插件】三级联动生成器插件`,
        reader: `【H5阅读器】基于HTML5的Web阅读器`,
        more: `>>更多`

      },
      showGitHub: false,
      showWebsite: false,
      showEmail: false,
      showNext: false,
      goPlus: false
    };
  },
  methods: {
    change(content) {
      return new Promise((resolve, reject) => {
        content == 'intro' ? this.showNext = true : '';
        this['show' + content] = true
        Array.prototype.forEach.call(this.infos[content], (item, index) => {
          setTimeout(() => {
            this[content] += item;
          }, index === 0 ? 80 : index*140);
          setTimeout(() => {
            resolve();
          }, this.infos[content].length*140);
        })
      });
    },

    show(content) {
      return new Promise((resolve, reject) => {
        this['show' + content] = true;
        setTimeout(() => {
          resolve();
        }, 1800);
      });
    }

  }
}
</script>

<style>
* {
  line-height: 1.5;
}
::selection {
  color: black;
  background-color: #3bfc31;
}

.github-enter-active {
  transition: all 1.8s ease;
}
.github-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.github-enter, .github-leave-active {
  transform: translateX(-100px);
  opacity: 0;
}

.website-enter-active {
  transition: all 1.8s ease;
}
.website-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.website-enter, .website-leave-active {
  transform: translateX(110px);
  opacity: 0;
}

.email-enter-active {
  transition: all 1.8s ease;
}
.email-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.email-enter, .email-leave-active {
  transform: translateY(110px);
  opacity: 0;
}

a {
  text-decoration: none;
  position: relative;
}

a::after {
  position: absolute;
  left: 0;
  bottom: -4px;
  content: "";
  width: 100%;
  height: 2px;
  background-color: #3bfc31;
  transform: scaleX(0);
  transition: .3s linear;
}

a:hover::after {
  transform: scaleX(1);
  transition: .3s linear;
}

a:link {
  color: #3bfc31;
}
a:visited {
  color: #3bfc31;
}
/*a:hover {
  text-decoration: underline;
}*/
  #app {
    width: 100%;
  }

  h1 {
    padding-top: 10px;
    text-align: center;
    transition: all .3s;
  }

  h1:hover {
    transform: rotate(15deg);
    transition: all .3s;
  }

  .next {
    position: fixed;
    font-size: 1.1rem;
    top: 80px;
    right: 40px;
  }

  @keyframes scaleIt {
    0% {
      transform: scale(1);
    }
    33% {
      transform: scale(1.5);
    }
    66% {
      transform: scale(1.5);
    }
    100% {
      transform: scale(1);
    }
  }

  .go {
    position: fixed;
    bottom: 12%;
    right: 23%;
    text-align: center;
    font-size: 22px;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    border: 2px solid #3bfc31;
    display: none;
  }

  .go-plus {
    animation: scaleIt 3s infinite;
    display: block;
  }

  .go i {
    display: inline-block;
    width: 100%;
    height: 100%;
    background: url('./go.svg') no-repeat;
    background-size: contain;
    background-position: 5px -5px;

  }

  #app p {
    text-indent: 1em;
    overflow: hidden;
    width: 100%;
    word-wrap: break-word;
  }
  .name, .major, .skills, .intro, .website, .github, .email, .achievements {
    padding-left: 100px;
    padding-top: 10px;
  }
  @media only screen and (max-width: 768px) {
    * {
      font-size: 96%;
    }

    .name, .major, .skills, .intro, .website, .github, .email, .achievements {
      padding-left: 10px;
      padding-top: 5px;
    }
    h1 {
      padding-top: 0;
      margin-bottom: 0;
      font-size: 130%;
    }
    * {
      line-height: 1.3;
    }
    .go {
      right: 29px;
      bottom: 25px;
    }
    .next {

      top: 40px;
    }
  }
  div {
    padding-top: 10px;
  }
</style>



